* {
    margin:0;
    padding:0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
}

html {
    font-family: "Montserrat", sans-serif;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-size: 16px;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}
